//scrollbar
* {
	&::-webkit-scrollbar {
		display: initial !important;
		&:vertical {
			width: 7px;
			@include elevate {
				background: linear-gradient(to right, transparent 3px, $elevateSecondary 3px, $elevateSecondary 4px, transparent 4px); }
			@include metro {
				background: linear-gradient(to right, transparent 3px, $metroSecondary 3px, $metroSecondary 4px, transparent 4px); } }
		&:horizontal {
			height: 7px;
			@include elevate {
				background: linear-gradient(to bottom, transparent 3px, $elevateSecondary 3px, $elevateSecondary 4px, transparent 4px); }
			@include metro {
				background: linear-gradient(to bottom, transparent 3px, $metroSecondary 3px, $metroSecondary 4px, transparent 4px); } } }
	&::-webkit-scrollbar-thumb {
		@include elevate {
			background: $elevatePrimary;
			@include hover {
				background: $elevatePrimary; } }
		@include metro {
			background: $metroPrimary;
			@include hover {
				background: $metroPrimary; } } } }
